import { loadState } from '@nextcloud/initial-state';
import Organisation from './Organisation';
import HeaderBar from '../shared/HeaderBar';
import { ACCOUNT_PROPERTY_READABLE_ENUM } from '../../../constants/AccountPropertyConstants';

var _loadState = loadState('settings', 'personalInfoParameters', {}),
    primaryOrganisation = _loadState.organisationMap.primaryOrganisation;

export default {
  name: 'OrganisationSection',
  components: {
    Organisation: Organisation,
    HeaderBar: HeaderBar
  },
  data: function data() {
    return {
      accountProperty: ACCOUNT_PROPERTY_READABLE_ENUM.ORGANISATION,
      primaryOrganisation: primaryOrganisation
    };
  }
};