import AddButton from './AddButton';
import FederationControl from './FederationControl';
import { ACCOUNT_PROPERTY_READABLE_ENUM, ACCOUNT_SETTING_PROPERTY_READABLE_ENUM, PROFILE_READABLE_ENUM } from '../../../constants/AccountPropertyConstants';
export default {
  name: 'HeaderBar',
  components: {
    AddButton: AddButton,
    FederationControl: FederationControl
  },
  props: {
    accountProperty: {
      type: String,
      required: true,
      validator: function validator(value) {
        return Object.values(ACCOUNT_PROPERTY_READABLE_ENUM).includes(value) || Object.values(ACCOUNT_SETTING_PROPERTY_READABLE_ENUM).includes(value) || value === PROFILE_READABLE_ENUM.PROFILE_VISIBILITY;
      }
    },
    isEditable: {
      type: Boolean,
      default: true
    },
    isMultiValueSupported: {
      type: Boolean,
      default: false
    },
    isValidSection: {
      type: Boolean,
      default: false
    },
    labelFor: {
      type: String,
      default: ''
    },
    scope: {
      type: String,
      default: null
    }
  },
  data: function data() {
    return {
      localScope: this.scope
    };
  },
  computed: {
    isProfileProperty: function isProfileProperty() {
      return this.accountProperty === ACCOUNT_PROPERTY_READABLE_ENUM.PROFILE_ENABLED;
    },
    isSettingProperty: function isSettingProperty() {
      return Object.values(ACCOUNT_SETTING_PROPERTY_READABLE_ENUM).includes(this.accountProperty);
    }
  },
  methods: {
    onAddAdditional: function onAddAdditional() {
      this.$emit('add-additional');
    },
    onScopeChange: function onScopeChange(scope) {
      this.$emit('update:scope', scope);
    }
  }
};