import { loadState } from '@nextcloud/initial-state';
import { subscribe, unsubscribe } from '@nextcloud/event-bus';
import EditProfileAnchorLink from './EditProfileAnchorLink';
import HeaderBar from '../shared/HeaderBar';
import ProfileCheckbox from './ProfileCheckbox';
import ProfilePreviewCard from './ProfilePreviewCard';
import { ACCOUNT_PROPERTY_READABLE_ENUM } from '../../../constants/AccountPropertyConstants';

var _loadState = loadState('settings', 'personalInfoParameters', {}),
    organisation = _loadState.organisationMap.primaryOrganisation.value,
    displayName = _loadState.displayNameMap.primaryDisplayName.value,
    profileEnabled = _loadState.profileEnabled,
    userId = _loadState.userId;

export default {
  name: 'ProfileSection',
  components: {
    EditProfileAnchorLink: EditProfileAnchorLink,
    HeaderBar: HeaderBar,
    ProfileCheckbox: ProfileCheckbox,
    ProfilePreviewCard: ProfilePreviewCard
  },
  data: function data() {
    return {
      accountProperty: ACCOUNT_PROPERTY_READABLE_ENUM.PROFILE_ENABLED,
      organisation: organisation,
      displayName: displayName,
      profileEnabled: profileEnabled,
      userId: userId
    };
  },
  mounted: function mounted() {
    subscribe('settings:display-name:updated', this.handleDisplayNameUpdate);
    subscribe('settings:organisation:updated', this.handleOrganisationUpdate);
  },
  beforeDestroy: function beforeDestroy() {
    unsubscribe('settings:display-name:updated', this.handleDisplayNameUpdate);
    unsubscribe('settings:organisation:updated', this.handleOrganisationUpdate);
  },
  methods: {
    handleDisplayNameUpdate: function handleDisplayNameUpdate(displayName) {
      this.displayName = displayName;
    },
    handleOrganisationUpdate: function handleOrganisationUpdate(organisation) {
      this.organisation = organisation;
    }
  }
};