    primaryBiography = _loadState.biographyMap.primaryBiography;

export default {
  name: 'BiographySection',
  components: {
    Biography: Biography,
    HeaderBar: HeaderBar
  },
  data: function data() {
    return {
      accountProperty: ACCOUNT_PROPERTY_READABLE_ENUM.BIOGRAPHY,
      primaryBiography: primaryBiography
